import React, { useState, useRef  } from "react";
import exportAsImage from "../utils/exportAsImage"
import MyContext from "./MyContext";

const MyProvider = ({ children }) => {

  const start = JSON.parse(localStorage.getItem("StartPage"))
   
  const [count, setCount] = useState(() => {
    const saved = localStorage.getItem("Games");
    const value = parseFloat(saved);
    return value || 0;
  });


  const [name, setName] = useState(() => {
    const saved = localStorage.getItem("Name");
    return saved || "";
  });


  const [show, setShow] = useState(start);
 
  
  const [display, setDisplay] = useState(true);


  const exportRef = useRef();

  const [userScore, setUserScore] = useState("");


  const date = new Date().toDateString().split(" ").join("_")


  const [scoreDisplay, setScoreDisplay] = useState(() => {
    const saved = localStorage.getItem("Score");
    const value = parseFloat(saved);
    return value || 0;
  });

  const gameCountAdder = (e) => {
    e.preventDefault();
    setCount((count) => count + 1);
  };

 
  const nameSubmitter = (e) => {
    setName(e.target.value);
  };

  const toggleInfo = (e) => {
    e.preventDefault();
    setDisplay(!display)
    
  };

  const handleScoreChange = (e) => {
    setUserScore(parseFloat(e.target.value));
  };


 
  const handleSubmit = (e) => {
    e.preventDefault();
    setScoreDisplay(prev=>prev+userScore);
    setUserScore("");
  };

  const nameSetter = (e) => {
    setName(e.target.value);
    
  }
  const hideStartPage = (e) => {
    e.preventDefault();
    setShow(false);

  }

  const saveSession = (e) => {
    e.preventDefault();
    localStorage.setItem("Score",scoreDisplay);
    localStorage.setItem("Name", name);
    localStorage.setItem("StartPage", show);
    localStorage.setItem("Games", count)

  }

  const resetter = (e) => {
    
    localStorage.setItem("Score",0);
    localStorage.setItem("Name", "");
    localStorage.setItem("StartPage", true);
    localStorage.setItem("Games", 0)

  }

  


  
  
  return (
    <MyContext.Provider
      value={{
        count,
        gameCountAdder,
        name,
        nameSubmitter,
        display,
        toggleInfo,
        userScore,
        handleScoreChange,
        handleSubmit,
        scoreDisplay,
        nameSetter,
        show,
        hideStartPage,
        saveSession,
        resetter,
        exportAsImage,
        exportRef,
        date
      }}
    >
      {children}
    </MyContext.Provider>
  );
};
export default MyProvider;
