import React, { useContext } from "react";
import styled from "styled-components";
import MyContext from "../context/MyContext";

const StyledMain = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 50vh;
`;
const Name = styled.div`
  font-family: "PressStart2P";
  height: 2rem;
  color: #2fff81;
  font-size: clamp(1rem, 0.8148rem + 0.9877vw, 1.5rem);
  text-shadow: 1px -1px 1px #fff, -1px 1px 2px #2fff81;
  margin-bottom: 5px;
`;
const DisplayScore = styled.div`
  /* display: block;
  margin: 0 auto; */
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  font-family: "SpaceMono";
  background: radial-gradient(
    ellipse 150px 1000px,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(101, 163, 45, 0.4) 59%,
    rgba(250, 150, 1, 0.5) 100%
  );
  font-size: 1rem;
  width: 300px;
  height: 15vh;
  color: rgba(20, 255, 255, 0.6);
  background-size: 120% 120%;
  animation: gradient 6s ease-in-out infinite;
  opacity: 1;

  @keyframes gradient {
    0% {
      background-position: 0% 50%;
    }

    50% {
      background-position: 100% 50%;
    }

    100% {
      background-position: 0% 50%;
    }
  }
`;
const Form = styled.form`
  margin-top: 1rem;

  width: 300px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 120px;
`;

const UpperForm = styled.div`
  display: grid;
  grid-template-columns: 70px 130px 70px;
  grid-template-rows: 1fr;
  width: 300px;
`;
const LowerForm = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  width: 300px;
`;
const CentralForm = styled.div`
  display: grid;
  grid-template-columns: 200px 100px;
  grid-template-rows: 1fr;
  width: 300px;
`;

const GameButton = styled.button`
  background-color: rgba(255, 255, 255, 0.1);
  width: 70px;
  height: 40px;
  display: flex;

  align-items: center;
  font-family: "PressStart2P";
  font-size: 1rem;
  color: white;
`;
const ResetButton = styled.button`
  width: 100px;
  height: 40px;
  font-family: "PressStart2P";
  font-size: 0.8rem;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
`;

const Save = styled.button`
  width: 200px;
  height: 40px;
  font-family: "PressStart2P";
  font-size: 0.8rem;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
`;

const ScoreInput = styled.input`
  background-color: rgba(255, 255, 255, 0.1);
  width: 120px;
  height: 35px;
  font-family: "PressStart2P";
  font-size: 0.8rem;
  color: #2fff81;
`;

const Footer = styled.footer`
  margin-top: 15px;
  font-family: "PressStart2P";
  font-size: 9px;
  color: white;
`;
const Main = () => {
  const {
    count,
    gameCountAdder,
    name,
    toggleInfo,
    userScore,
    handleScoreChange,
    handleSubmit,
    scoreDisplay,
    saveSession,
    resetter,
    exportAsImage,
    exportRef,
    date,
  } = useContext(MyContext);

  return (
    <StyledMain>
      <Name>Welcome, {name}</Name>
      <DisplayScore ref={exportRef}>
        Your current total is {scoreDisplay}. You have played {count} Wordles.{" "}
        Your average per game is{" "}
        {(parseFloat(scoreDisplay) / parseFloat(count)).toFixed(2)} :)
      </DisplayScore>

      <Form>
        <UpperForm>
          <GameButton
            onClick={(e) => gameCountAdder(e)}
            style={{ justifySelf: "end" }}
          >
            +
          </GameButton>

          <ScoreInput
            type="number"
            onChange={(e) => handleScoreChange(e)}
            placeholder="tries?"
            value={userScore}
          />
          <ResetButton onClick={(e) => handleSubmit(e)}>Submit</ResetButton>
        </UpperForm>
        <CentralForm>
          <Save onClick={(e) => saveSession(e)}>Save</Save>
          <ResetButton
            onClick={(e) => {
              e.preventDefault();
              exportAsImage(exportRef.current, `Wordle_${date}`);
            }}
          >
            export
          </ResetButton>
        </CentralForm>
        <LowerForm>
          <ResetButton>Undo</ResetButton>
          <ResetButton onClick={(e) => toggleInfo(e)}>Info</ResetButton>
          <ResetButton
            onClick={(e) => resetter(e)}
            style={{ backgroundColor: "red", color: "white" }}
          >
            Reset
          </ResetButton>
        </LowerForm>
      </Form>
      <Footer>© Martin Bittner 2022</Footer>
    </StyledMain>
  );
};

export default Main;
